
'use client';

import React from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import type { Repair, Technician } from '@/lib/types';
import { format } from 'date-fns';

interface ArchivedRepairsListProps {
  repairs: Repair[];
  technicians: Technician[];
  onViewDetails: (repair: Repair) => void;
}

export function ArchivedRepairsList({ repairs, technicians, onViewDetails }: ArchivedRepairsListProps) {

  const getTechnicianName = (techIdentifier: string): string => {
    if (!technicians || !techIdentifier) return 'Unassigned';
    
    const identifier = techIdentifier.toLowerCase();
    
    const technicianByEmail = technicians.find(t => t.email.toLowerCase() === identifier);
    if (technicianByEmail) return technicianByEmail.name;

    const technicianByName = technicians.find(t => t.name.toLowerCase() === identifier);
    if (technicianByName) return technicianByName.name;

    return techIdentifier;
  };

  const archivedRepairs = (repairs || []).filter(r => r.status === 'Archived');

  return (
    <Card>
      <CardHeader>
        <CardTitle>Archived Repairs</CardTitle>
        <CardDescription>
          A list of all completed and archived repair jobs.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Customer</TableHead>
              <TableHead>Device</TableHead>
              <TableHead className="hidden lg:table-cell">Serviced By</TableHead>
              <TableHead className="hidden sm:table-cell">Created</TableHead>
              <TableHead className="hidden md:table-cell">Archived</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {archivedRepairs.length > 0 ? (
                archivedRepairs.map((repair) => (
                    <TableRow key={repair.id} onClick={() => onViewDetails(repair)} className="cursor-pointer">
                        <TableCell>
                            <div className="font-medium">{repair.customerName}</div>
                        </TableCell>
                        <TableCell>{repair.brand} {repair.model}</TableCell>
                        <TableCell className="hidden lg:table-cell">{getTechnicianName(repair.assignedToName)}</TableCell>
                        <TableCell className="hidden sm:table-cell">{format(repair.createdAt, 'PP')}</TableCell>
                        <TableCell className="hidden md:table-cell">{repair.archivedAt ? format(repair.archivedAt, 'PP') : 'N/A'}</TableCell>
                    </TableRow>
                ))
            ) : (
                <TableRow>
                    <TableCell colSpan={5} className="text-center h-24">
                        No archived repairs found.
                    </TableCell>
                </TableRow>
            )}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  );
}
